.controls-div {
  grid-row: 2 / 3;
  grid-column: 4 / 6;

  min-width: 200px;

  border: 5px solid black;
  border-radius: 10px;

  text-align: center;

  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--controls-bck);
}

.controls-title-div {
  display: flex;
}

.controls-title {
  color: var(--pad);
}

.backwards {
  color: var(--pad);
  -webkit-transform:rotateY(180deg);
  -moz-transform:rotateY(180deg);
  -o-transform:rotateY(180deg);
  -ms-transform:rotateY(180deg);
  transform:rotateY(180deg);
}


#sliders-div {
  width: 95%;
  height: 45%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  
  /* border: 1px solid black; */
}

.slider-div {
  
  /* border: 1px solid red; */
}

.slider-value {
  width: 90%;

  color: var(--menu-red);
  background-color: black;
  
  margin: 40px auto 0;
  /* border: 1px solid green; */
}

.slider-label {
  font-size: 15px;
  color: var(--pad);
  margin: 5px 0;
  /* border-bottom: 1px solid var(--pad); */
}

#sliders-div input {
  box-shadow: 1.5px -1.5px 0px 0px var(--pad-dark);
  margin-top: 60px;
}

input[type=range] {
  transform: rotate(-90deg);
}

#current-pad-display {
  width: 90%;
  height: 60px;
  margin: auto;

  background-color: #000000;
  border: 1px solid var(--controls-bcks);
  border-radius: 5px;
  
  font-size: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#current-pad-display:hover {
  cursor: pointer;
}

#display {
  margin: auto;
  font-size: 25px;
  text-align: center;
  color: #972727;
  background-color: #000000;
  border: none;
}

/*SLIDER SLIDER SLIDER SLIDER SLIDER SLIDER */
input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type=range]:focus {
  outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}

input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;
  
  /* Hides the slider so custom styles can be added */
  background: transparent; 
  border-color: transparent;
  color: transparent;
}

/* Special styling for WebKit/Blink */
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 1px solid #000000;
  height: 36px;
  width: 12px;
  border-radius: 3px;
  background: var(--pad);
  cursor: pointer;
  margin-top: -15px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  /* box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; */
  /* box-shadow: -1px 1px 0px 0px var(--pad-dark); */
}

/* All the same stuff for Firefox */
input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 12px;
  border-radius: 3px;
  background: var(--pad);
  cursor: pointer;
  box-shadow: 1px 1px 0px 0px var(--pad-dark);
}

/* All the same stuff for IE */
input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 12px;
  border-radius: 3px;
  background: var(--pad);
  cursor: pointer;
  box-shadow: 1px 1px 0px 0px var(--pad-dark);
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: var(--slider-bck);
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: var(--slider-bck);
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: var(--slider-bck);
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}

input[type=range]::-ms-track {
  width: 100%;
  height: 7px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: var(--slider-bck);
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]:focus::-ms-fill-lower {
  background: var(--slider-bck);
}
input[type=range]::-ms-fill-upper {
  background: var(--slider-bck);
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]:focus::-ms-fill-upper {
  background: var(--slider-bck);
}
/*END SLIDER END SLIDER END SLIDER END SLIDER END SLIDER END SLIDER */